import React from 'react'

const Dashboard = React.lazy(() => import('./views/dashboard/Dashboard'))
const ListUsersDetail = React.lazy(() => import('./views/user/listUsersDetail'))
const ListShop = React.lazy(() => import('./views/shops/ListShop'))
const StatisticCustomer = React.lazy(() => import('./views/statistics/StatisticCustomer'))
const StatisticSeller = React.lazy(() => import('./views/statistics/StatisticSeller'))
const StatisticShop = React.lazy(() => import('./views/statistics/StatisticShop'))

const routes = [
  { path: '/admin', name: 'Tng quan', exact: true, component: Dashboard },
  { path: '/admin/manage-users', name: 'Qun l ngi dng', component: ListUsersDetail },
  { path: '/admin/list-shops', name: 'Danh sch ca hng', component: ListShop },
  { path: '/admin/statistic-customer', name: 'Chi tiu khch hng', component: StatisticCustomer },
  { path: '/admin/statistic-seller', name: 'Doanh thu ngi bn hng', component: StatisticSeller },
  { path: '/admin/statistic-shop', name: 'Doanh thu ca hng', component: StatisticShop }
]

export default routes
